import React, { useEffect, useState } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import * as yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import AddIcon from "@material-ui/icons/Add";
import ClearIcon from "@material-ui/icons/Clear";
import {
  CircularProgress,
  withStyles,
  Tooltip,
  IconButton,
  Box,
  FormControl,
  InputLabel,
  FormHelperText,
  MenuItem,
  Select as MuiSelect
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

var initialValues = {
  id: 0,
  fiber_percentages_id: [""],
};

function duplicatePercentageCheck(values){
  if(values.fiber_percentages_id.length == new Set(values.fiber_percentages_id).size){
    return true
  }
  return false
}

function totalPercentageCheck(fiberPercentages, values){
  let sum = 0;
  for (let i=0; i<values.fiber_percentages_id.length; i++){
    fiberPercentages.filter(fiberPercentage => {
        if(fiberPercentage.id === values.fiber_percentages_id[i]){
          sum+= parseFloat(fiberPercentage.percentage)
        }
      }
    )
  }
  return sum
}

function duplicateCompositionCheck(compositionList, values){
  for(let i = 0; i< compositionList.length; i++){
    if(values.fiber_percentages_id.length === compositionList[i].fiber_percentages.length){
      var count = 0;
      for (let k=0; k<values.fiber_percentages_id.length; k++){
        for(let j = 0; j<compositionList[i].fiber_percentages.length; j++){
          if(values.fiber_percentages_id[k] === compositionList[i].fiber_percentages[j].id){
            count += 1
          }
        }
      }
      if(values.fiber_percentages_id.length === count){
        return false
      }
    }
  }
  return true
}

const CompositionForm = (props) => {
  const [fiberPercentages, setFiberPercentages] = useState([]);
  const { addOrEdit, recordForEdit, compositionList } = props;
  const [ errorMessage, setErrorMessage ] = useState("")

  const validationSchema = yup.object().shape({
    fiber_percentages_id: yup
      .array()
      .of(yup.string().trim()
      .required("FIber percentage is required")
      ),
  });

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(false);
      if(duplicatePercentageCheck(values)){
        setErrorMessage("")
        var totalPercentage = totalPercentageCheck(fiberPercentages, values)
        if(totalPercentage>100 || totalPercentage<100){
          setErrorMessage("Fiber percentage is not 100%")
        }else{
          setErrorMessage("")
          if(duplicateCompositionCheck(compositionList, values)){
            addOrEdit(values, resetForm, setSubmitting);
          }else{
            setErrorMessage("Composition needs to be unique")
          }
        }
      }else{
        setErrorMessage("Fiber percentage needs te be unique.")
      }
    },
  });

  function handleAdd() {
    const values = [...formik.values.fiber_percentages_id];
    values.push("");
    formik.setFieldValue("fiber_percentages_id", values);
  }

  function handleRemove(i) {
    const values = [...formik.values.fiber_percentages_id];
    values.splice(i, 1);
    formik.setFieldValue("fiber_percentages_id", values);
  }

  function handleChange(i, event) {
    const values = [...formik.values.fiber_percentages_id];
    values[i] = event.target.value;
    formik.setFieldValue("fiber_percentages_id", values);
    setErrorMessage("")
  }

  useEffect(() => {
    async function getFiberPercentages() {
      const response = await fetch("/api/fiber_percentage/list");
      const body = await response.json();
      setFiberPercentages(body);
    }
    getFiberPercentages();
  }, []);

  useEffect(() => {
    if (recordForEdit != null)
      formik.setValues({
        ...recordForEdit,
      });
  }, [recordForEdit]);

  return (
    <Form onSubmit={formik.handleSubmit}>
      <DialogContent dividers>
        {formik.values.fiber_percentages_id.map((field, idx) => {
          return (
            <Box display="flex" pb={1} key={`${field}-${idx}`}>
              <FormControl
                variant="outlined"
                fullWidth
                {...(formik.touched.fiber_percentages_id &&
                  !formik.values.fiber_percentages_id[idx] && {error:Boolean(formik.errors.fiber_percentages_id)})}
              >
                <InputLabel>Fiber Percentage</InputLabel>
                <MuiSelect
                  label="Fiber Percentage"
                  name={`fiber_percentages_id-${idx}`}
                  value={field || ""}
                  onChange={(e) => handleChange(idx, e)}
                >
                  <MenuItem value="">None</MenuItem>
                  {fiberPercentages.map((item) => (
                    <MenuItem key={item.id} value={item.id}>
                      {item.name}
                    </MenuItem>
                  ))}
                </MuiSelect>
                {formik.touched.fiber_percentages_id && !formik.values.fiber_percentages_id[idx] && Boolean(formik.errors.fiber_percentages_id) && <FormHelperText>FIber percentage is required</FormHelperText>}
              </FormControl>
              {/* <Controls.Select
                label="Fiber Percentage"
                name={`fiber_percentages-${idx}`}
                value={field || ""}
                onChange={(e) => handleChange(idx, e)}
                // onBlur={formik.handleBlur}
                options={fiberPercentages}
                error={
                  formik.touched.fiber_percentages &&
                  !formik.values.fiber_percentages[idx] &&
                  Boolean(formik.errors.fiber_percentages)
                }
                helperText={
                  formik.touched.fiber_percentages &&
                  formik.errors.fiber_percentages[idx]
                }
              /> */}
              {(() => {
                if (idx !== 0) {
                  return (
                    <Tooltip title={"Remove"}>
                      <IconButton
                        onClick={() => {
                          handleRemove(idx);
                        }}
                      >
                        <ClearIcon />
                      </IconButton>
                    </Tooltip>
                  );
                }

                return (
                  <Tooltip title={"Add New"}>
                    <IconButton
                      onClick={() => {
                        handleAdd();
                      }}
                    >
                      <AddIcon />
                    </IconButton>
                  </Tooltip>
                );
              })()}
            </Box>
          );
        })}
        {/* <Grid container justify="flex-end">
          <Tooltip title={"Add New"}>
            <IconButton
              onClick={() => {
                handleAdd();
              }}
            >
              <AddIcon />
            </IconButton>
          </Tooltip>
        </Grid> */}
        <p style={{fontSize: "15px", margin: "0px", color: "red"}}>{errorMessage.length > 0 ? errorMessage : null}</p>
      </DialogContent>
      <DialogActions>
        <div className={classes.wrapper}>
          <Controls.Button
            type="submit"
            disabled={formik.isSubmitting}
            text="Submit"
          />
          {formik.isSubmitting && (
            <CircularProgress size={24} className={classes.buttonProgress} />
          )}
          <Controls.Button
            text="Reset"
            color="default"
            onClick={formik.resetForm}
          />
        </div>
      </DialogActions> 
    </Form>
  );
};

export default CompositionForm;
