import React, { useEffect } from "react";
import { Form } from "../../components/Form/useForm";
import Controls from "../../components/Controls/Controls";
import { useFormik } from "formik";
import * as yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Grid,
  CircularProgress,
} from "@material-ui/core";

const style = makeStyles({
  wrapper: {
    position: "relative",
  },

  buttonProgress: {
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12,
  },
});

var initialValues = {
  id: 0,
  fiber: "",
  percentage: "",
};

let fiberId = 0;
function duplicatePercentageCheck(fiberPercentageList, value, fiberId, fibers, recordForEdit){
  let check = true
  if(fiberPercentageList.length === 0){
    return check
  }else{
    for(let a = 0; a< fibers.length; a++){
      if(fibers[a].id === parseInt(fiberId)){
        for(let i =0; i< fiberPercentageList.length; i++){
          let lastIndex = fiberPercentageList[i].name.lastIndexOf(" ")
          let name = fiberPercentageList[i].name.substring(0, lastIndex)
          if(recordForEdit){
            if(recordForEdit.fiber === parseInt(fiberId) && parseFloat(recordForEdit.percentage) === value){
              return check = true
            }else{
              if(fibers[a].name === name && parseFloat(fiberPercentageList[i].percentage) === parseFloat(value)){
                return check = false
              }
            }
          }else{
            if(fibers[a].name === name.trim() && parseFloat(fiberPercentageList[i].percentage) === parseFloat(value)){
              return check = false
            }
          }
        }
        return check
      }
    }
  }
}

const FiberPercentageForm = (props) => {

  const [fibers, setFibers] = React.useState([]);

  const { addOrEdit, recordForEdit, fiberPercentageList } = props;

  const validationSchema = yup.object().shape({
    fiber: yup.string().required("Fiber is required")
    .test((values) => {
      return fiberId = values
    }),
    percentage: yup.number()
      .required("Percentage is required")
      .typeError("Invalid Input: numbers please")
      .positive("Must be greater than zero")
      .test("Unique", "Percentage needs te be unique", (values) => {
        // console.log(fiberPercentageList)
        // console.log("values: ", values)
        // console.log("fiberId: ", fiberId)
        // console.log("fibers: ",fibers)
        return duplicatePercentageCheck(fiberPercentageList, values, fiberId, fibers, recordForEdit)
      })
      .lessThan(100.000001, "Percentage must be less than 100%"),
  });

  const classes = style();

  const formik = useFormik({
    initialValues: initialValues,
    validationSchema: validationSchema,
    onSubmit: (values, { setSubmitting, resetForm }) => {
      setSubmitting(true);
      addOrEdit(values, resetForm, setSubmitting);
    },
  });

  useEffect(() => {
    async function getFibers() {
      const response = await fetch("/api/fiber/list");
      const body = await response.json();
      setFibers(body);
    }
    getFibers();

    if (recordForEdit != null)
      formik.setValues({
        ...recordForEdit,
      });
  }, [recordForEdit]);


  return (
    <Form onSubmit={formik.handleSubmit}>
      <Grid container alignItems="flex-start" spacing={2}>
        <Grid item md={12} sm={12} xs={12}>
          <Controls.Select 
            label="Fiber"
            name="fiber"
            value={formik.values.fiber}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            options={fibers}
            error={formik.touched.fiber && Boolean(formik.errors.fiber)}
            helperText={formik.touched.fiber && formik.errors.fiber}
          />
        </Grid>

        <Grid item md={12} sm={12} xs={12}>
          <Controls.Input
            label="Percentage"
            name="percentage"
            id="percentage"
            value={formik.values.percentage}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.percentage && Boolean(formik.errors.percentage)}
            helperText={formik.touched.percentage && formik.errors.percentage}
            fullWidth
          />
        </Grid>
        <Grid item style={{ marginTop: 16 }}>
          <div className={classes.wrapper}>
            <Controls.Button
              type="submit"
              disabled={formik.isSubmitting}
              text="Submit"
            />
            {formik.isSubmitting && (
              <CircularProgress size={24} className={classes.buttonProgress} />
            )}
            <Controls.Button
              text="Reset"
              color="default"
              onClick={formik.resetForm}
            />
          </div>
        </Grid>
      </Grid>
    </Form>
  );
};

export default FiberPercentageForm;
